.products-search{
  display: flex;
  background-color: var(--surface-200);
  border-radius: 10px;
  width: 275px;
  height: 35px;
}

.products-search .pi{
  width: 1.3em;
  font-size: 1.3em;
  color: #ff3a00;
  margin-top: 10px;
  margin-left: 10px;
}


.products-search-input{
  height: 35px;
  background-color: transparent;
  color: var(--surface-600);
  border: 0;
}


.products-search-input:focus{
  outline: none;
  border: 0;
}
