.product-name{
  font-size: 2em;
}

.product-desc{
  font-size: 1.4em;
}

.product-rating{
  margin: 15px 0;
}

.product-price{
  font-size: 2em;
  font-weight: 600;
  border-bottom: 1px solid var(--surface-300);
  padding: 25px 0;
}


.price-before{
  font-size: 0.7em;
  margin-left: 10px;
  font-weight: 200;
  color: var(--surface-500);
  text-decoration: line-through;
}

.product-quantity{
  margin: 25px 0;

  label{
    font-size: 1.2em;
  }
}

.p-button{
  background-color: #ff3a00;
  border: 0;
}


.product-rich-description{
  line-height: 1.6;
}
